rd of a in-development game. This dashboard will allow a player to look through the different policies attributed to different roles (e.g. “Prime Minister”, “Minister of Labor”, “Minister of Health”, and so on) and to select from a couple of options on how they would like to enact that policy. For a demonstration of how this was implemented in past games, you can check out the "),_c('a',{attrs:{"href":"https://www.clsgbatten.org/","target":"__blank"}},[_vm._v("CLSG website")]),_vm._v(". ")])]),_c('v-row',[_c('v-col',{staticClass:" pt-0 pb-0",attrs:{"cols":"12"},on:{"click":function($event){return _vm.showImg(3)}}},[_c('v-img',{staticClass:"img",attrs:{"src":"https://julielinzhou.files.wordpress.com/2019/07/screen-shot-2019-07-16-at-2.55.19-pm-1.png","contain":""}})],1)],1),_c('v-row',[_c('v-card-text',[_c('p',[_vm._v("Above, you can see the main features of the dashboard, there is a toolbar at the top of the app, along with the two constant collapse/expansion panels “Graphs” and “City Metrics”. The graphs were created with random data and using "),_c('a',{attrs:{"href":"https://vue-chartjs.org/","target":"__blank"}},[_vm._v("vue-chartjs")]),_vm._v(" which is a wrapper for Chart.js in Vue. I created a separate component for the graphs and called it twice on this page. ")]),_c('p',[_vm._v("Below that, I have tabs of different “Sectors” and their metrics, as well as the “Roles” expansion panels. ")])])],1),_c('v-row',[_c('v-col',{staticClass:"pt-0 pb-0",attrs:{"cols":"12"}},[_c('v-img',{staticClass:"img",attrs:{"src":"https://julielinzhou.files.wordpress.com/2019/07/screen-shot-2019-07-16-at-2.56.51-pm.png","contain":""},on:{"click":function($event){return _vm.showImg(4)}}})],1)],1),_c('v-row',[_c('v-card-text',[_c('p',[_vm._v("Inside of each panel, I have nested the expansion panels for each “Policy” as well as a drop-down select box. All of the “Roles” information was taken from an external API. ")]),_c('p',[_vm._v(" The default values of the select boxes are saved i