,[_c('v-icon',[_vm._v("mdi-github-circle")])],1)],1),_c('v-divider',{staticClass:"mt-0 mb-0"}),_c('v-card-subtitle',{staticClass:"font-weight-bold subtitle-1"},[_vm._v("Summary")]),_c('v-card-text',{staticClass:"pb-0"},[_c('p',[_vm._v("This is a project that I undertook along with a partner, Winnie Zhang, in my senior year of high school. The conception of this project happened because we were selected to represent our magnet school, Governor’s School for Science and Technology in a program called Future Leaders Pavilion at a military convention in Orlando, FL called Interservice/Industry Training, Simulation and Education Conference (I/ITSEC). This simulation was created in NetLogo, which is an agent-based modeling software, and used a free world map shapefile, which stored attributes for every pixel (such a population) from the Natural Earth database in order to produce the visual changes in the simulation. ")])]),_c('v-card-subtitle',{staticClass:"font-weight-bold subtitle-1 pb-0"},[_vm._v("Key Learning Points")]),_c('v-card-text',{staticClass:"pb-0"},[_c('v-list',{staticClass:"pt-0 pb-0"},[_c('v-list-item',[_c('v-list-item-icon',[_c('v-icon',[_vm._v("mdi-star")])],1),_c('v-list-item-title',{staticClass:"body-2"},[_vm._v("Challenged myself to research a difficult topic")])],1),_c('v-list-item',[_c('v-list-item-icon',[_c('v-icon',[_vm._v("mdi-star")])],1),_c('v-list-item-title',{staticClass:"body-2"},[_vm._v("Presented at a conference and published a research paper")])],1),_c('v-list-item',[_c('v-list-item-icon',[_c('v-icon',[_vm._v("mdi-star")])],1),_c('v-list-item-title',{staticClass:"body-2"},[_vm._v("Exchanging ideas and working closely with a partner")])],1)],1)],1),_c('v-expansion-panels',[_c('v-expansion-panel',[_c('v-expansion-panel-header',{staticClass:"font-weight-bold"},[_vm._v("More Details")]),_c('v-expansion-panel-content',[_c('v-row',[_c('v-card-text',[_c('p',[_vm._v("The initial question we had was whether or not it would be possible to predict the changes in population of the world if a World War III were to occur. We did not include a lot of factors such as political ties or nuclear weaponry due to the limited time we had and its complexity. First we gathered current population stats for every country and inputted them into the map shapefile. Next, we based the simulation’s “warfare” on the past two world wars so we hand-gathered casualty data from online databases and put that information in as well. The casualties in the simulation occurred at the same time as in the past world wars for instance, if the first battle occurred on July 14, that would be Day 1 in the model counter and then the next battle on July 25, would be displayed as Day 11. Along with the casualty data, we multiplied it by the weapon efficiency in order to simulate new casualty rates. ")]),_c('p',[_vm._v("Although we were not able to answer our initial question with this model, I think it would be useful for history classes to visualize international warfare and its impact on world population. ")])])],1),_c('v-row',[_c('v-col',{attrs:{"cols":"12"},on:{"click":function($event){return _vm.showImg(7)}}},[_c('v-img',{staticClass:"img",attrs:{"src":"https://julielinzhou.files.wordpress.com/2019/01/10ww2change-1.png","contain":""}})],1)],1),_c('v-row',[_c('v-card-subtitle',{staticClass:"font-weight-bold subtitle-1"},[_vm._v("Key Features")]),_c('v-card-text',[_c('ul',[_c('li',[_vm._v("World Map with countries in different shades of color depending on the size of their population today")]),_c('li',[_vm._v("User-adjustable inputs on the left side")]),_c('li',[_vm._v("Adjustable inputs include: weapon efficiency, which country’s population to monitor, which dataset to use (World War I or World War II), and number of soldiers")])])])],1),_c('v-row',[_c('v-col',{attrs:{"cols":"12"},on:{"click":function($event){return _vm.showImg(8)}}},[_c('v-img',{staticClass:"img",attrs:{"src":"https://julielinzhou.files.wordpress.com/2019/01/iitsec_flowchart.png